/* portfolio */
#portfolio{
    position:relative;
    width:100%;
    padding-top:3rem;
    box-sizing: border-box;
    display:table;
    text-align: center;
  }
  .box{
    /* width:67.5rem; */
    width:95%;
    display: flex;
    box-sizing: border-box;
    margin:0 auto;
  }
  .box>li{
    width:15rem;
  }
  .box>li:not(:last-child){
    margin-right:4rem;
  }
  .card{
    position:relative;
    width:18rem;
    height:19rem;
    box-sizing: border-box;
    box-shadow: 5px 11px 30px rgba(0,0,0,0.3);
    background-color: #fff;
    text-align: left;
    font-size:.8rem;
    cursor: pointer;
    margin-top:2rem;
    border-radius:10px;
    transition:0.1s ease-in-out;
  }
  p.img{
    border-radius:0 0 10px 10px;
    padding:1rem 1rem 4.7rem;
    background-color: #f7fa30;
  }
  .card02>p.img{
    display:table;
    padding:1rem 1rem 1.7rem;
  }
  .card02>p.img>img{
    width:16rem;
    margin:0 auto;
  }
  .card:hover{
    transform:translateY(-10px);
    box-shadow: 5px 11px 30px rgba(0,0,0,0.8);
  }  
  .card>dl>dt{
  padding:1rem 1rem 0;
  font-size: .9rem;
  } 
  .card>dl>dd{
    padding:0 1rem 2rem;
    margin-top:1rem;
  }
  .card04>p.img{
    padding-bottom:5.5rem;
  }
  .spanText{
    font-weight:bold;
  }
  .detail{
    font-family: 'Gowun Batang', serif;
    font-size:0.88rem;
  }
  /* tablet_size css */
  .Tbox{
    display: flex;
    height:62rem;
  }
  .cardT{
    position:absolute;
    width:18rem;
    height:19rem;
    box-sizing: border-box;
    box-shadow: 5px 11px 30px rgba(0,0,0,0.3);
    background-color: #fff;
    text-align: left;
    font-size:0.88rem;
    cursor: pointer;
    border-radius:10px;
    transition:0.1s ease-in-out;
    top:4rem;
  }
  p.imgT{
    border-radius:0 0 10px 10px;
    padding:1rem 1rem 4rem;
    background-color: #f7fa30;
    margin-top:1rem;
  }
  .cardT02>p.imgT{
    display:table;
    padding:1rem;
  }
  .cardT02>p.img>img{
    width:16rem;
    margin:0 auto;
  }
  .cardT:hover{
    transform:translateY(-10px);
    box-shadow: 5px 11px 30px rgba(0,0,0,0.8);
  }  
  .cardT>dl>dt{
  text-align: center;
  padding:1rem 1rem 0;
  } 
  .cardT>dl>dd{
    padding:0 1rem 1rem;
  }
  .spanText{
    font-weight:bold;
  }
  .detail{
    font-family: 'Gowun Batang', serif;
    font-size:0.85rem;
  }
  .cardT01{
    left:20%;
  }
  .cardT02{
    left:50%;
  }
  .cardT03{
    top:34rem;
    left:20%;
  }
  .cardT04{
    top:34rem;
    left:50%;
  }
  /* Mobile css */
  .Mbox{
    width:18rem;
    margin:0 auto;
  }
  .cardM{
    width:18rem;
    height:21rem;
    box-sizing: border-box;
    box-shadow: 5px 11px 30px rgba(0,0,0,0.3);
    background-color: #fff;
    text-align: left;
    font-size:0.88rem;
    cursor: pointer;
    border-radius:10px;
    transition:0.1s ease-in-out;
  }
p.imgM{
  border-radius:0 0 10px 10px;
  padding:1rem 1rem 4rem;
  background-color: #f7fa30;
  margin-top:1rem;
}
.cardM02>p.imgM{
  display:table;
  padding:1rem;
  margin-top:1rem;
}
.cardM02>p.img>img{
  width:16rem;
  margin:0 auto;
}
.cardM:hover{
  transform:translateY(-10px);
  box-shadow: 5px 11px 30px rgba(0,0,0,0.8);
}
.cardM>dl>dt{
